import { Injectable } from '@angular/core';

import * as firebase from 'firebase/app';
import {Observable} from 'rxjs';
import {AngularFireAuth} from '@angular/fire/auth';
import {Router} from '@angular/router';
import { Toast } from '@ionic-native/toast/ngx';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
    user: Observable<firebase.User>;
    private userLoggedIn = false;

    constructor(private firebaseAuth: AngularFireAuth, private toast: Toast, private router: Router) {
        this.user = firebaseAuth.authState;
    }

    register(email: string, password: string) {
        this.firebaseAuth
            .auth
            .createUserWithEmailAndPassword(email, password)
            .then(() => {
                this.toast.show(`Boli ste úspešne registrovaný!`, '3000', 'bottom').subscribe(
                    toast => {
                        this.router.navigate(['/home']);
                    }
                );
            })
            .catch(err => {
                this.toast.show(`Something went wrong!` + err, '3000', 'bottom').subscribe(
                    toast => {
                        this.router.navigate(['/home']);
                    }
                );
            });
    }

    login(email: string, password: string) {
      if (this.userLoggedIn) {
          this.toast.show(`Uz ste prihlásený!`, '3000', 'bottom').subscribe(
              toast => {
                  this.router.navigate(['/home']);
              }
          );
      } else {
          this.firebaseAuth
              .auth
              .signInWithEmailAndPassword(email, password)
              .then(value => {
                  this.router.navigate(['/home']);
                  this.toast.show(`Prihlásený!`, '3000', 'bottom').subscribe(
                      toast => {
                          this.router.navigate(['/home']);
                      }
                  );
              })
              .catch(err => {
                  this.toast.show('Something went wrong!' + err, '3000', 'bottom').subscribe(
                      toast => {
                          this.router.navigate(['/register']);
                      }
                  );
              });
      }
    }

    logout() {
      if (this.isLoggedIn) {
          this.firebaseAuth
              .auth
              .signOut()
              .then((done) => {
                this.toast.show('Logged Out!', '3000', 'bottom').subscribe(
                    toast => {
                        this.router.navigate(['/home']);
                    }
                );
          });
      }
    }

    get isLoggedIn() {
        return !!this.firebaseAuth.auth.currentUser;
    }

}
